<template>
  <header :class="['app-header', {'app-header-slide': slide}]">
    <div class="app-header-box">
      <router-link class="header-link" to="/">
        <svg-icon
          icon-class="logo"
          class="logo-icon"
        ></svg-icon>
      </router-link>
      <div class="header-link-nav">
        <router-link class="header-link" to="/school-class">學院課程</router-link>
        <router-link class="header-link" to="/">精選課程</router-link>
        <router-link class="header-link" to="/">實作設計</router-link>
        <router-link class="header-link" to="/about-us">關於我們</router-link>
        <confirm-button
          class="header-login"
          :text="'登入'"
          :loginType="true"
        ></confirm-button>
        <confirm-button
          :text="'註冊'"
        ></confirm-button>
      </div>
      <div v-if="showBurger" @click="switchMenu">
        <svg-icon
          icon-class="burger"
          class="burger-icon"
        ></svg-icon>
      </div>
      <div v-if="menu"
        class="header-menu"
      >
        <router-link class="header-menu-item" to="/school-class"><div @click="closeMenu">學院課程</div></router-link>
        <router-link class="header-menu-item" to="/"><div @click="closeMenu">精選課程</div></router-link>
        <router-link class="header-menu-item" to="/"><div @click="closeMenu">實作設計</div></router-link>
        <router-link class="header-menu-item" to="/about-us"><div @click="closeMenu">關於我們</div></router-link>
        <div @click="closeMenu" class="header-menu-item">免費註冊</div>
        <div @click="closeMenu" class="header-menu-item">登入</div>
      </div>
    </div>
  </header>
</template>
<script>
import ConfirmButton from '@/components/button/ConfirmButton'

export default {
  name: 'AppHeader',
  components: {
    ConfirmButton
  },
  data () {
    return {
      slide: false,
      showBurger: false,
      menu: false
    }
  },
  mounted () {
    window.addEventListener('scroll', this.handleScroll)
    document.addEventListener('click', this.autoHide, false)
    this.winWidth()
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScroll)
    document.removeEventListener('click', this.autoHide, false)
  },
  methods: {
    handleScroll () {
      let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      // let offsetTop = document.querySelector('.home-white-line').offsetTop
      if (scrollTop > 40) {
        this.slide = true
      } else {
        this.slide = false
      }
    },
    autoHide (evt) {
      if (!this.menu) return false
      if (!this.$el.contains(evt.target)) {
        this.menu = false
      }
    },
    switchMenu () {
      this.menu = !this.menu
    },
    closeMenu () {
      this.menu = false
    },
    winWidth () {
      var w = document.body.clientWidth
      if (w < 500) {
        this.showBurger = true
      } else {
        this.showBurger = false
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.app-header {
  width: 100%;
  height: auto;
  position: fixed;
  top: 0px;
  left: 50%;
  transform: translate(-50%, 0%);
  z-index: 2;

  &-slide {
    border-bottom: 1.5px solid #e5e9ee;
  }

  &-box {
    max-width: 1200px;
    width: 100%;
    height: 117px;
    margin: auto;
    padding: 0px 48px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #fff;
  }

  .logo-icon {
    width: 97px;
    height: 40px;

    &:hover {
      opacity: 0.8;
      cursor: pointer;
    }
  }

  .header {

    &-link-nav {
      display: flex;
      align-items: center;
    }

    &-link {
      color: #878a91;
      margin-right: 40px;

      &:hover {
        opacity: 0.8;
        cursor: pointer;
      }

      &:nth-child(4) {
        margin-right: 60px;
      }
    }

    &-login {
      margin-right: 20px;
    }
  }

  .burger-icon {
    display: none;
  }
}

@media( max-width: 500px ){

  .app-header {

    &-box {
      height: 52px;
    }

    .header {

      &-link-nav {
        display: none;
      }

      &-menu {
        position: fixed;
        right: 0;
        top: 52px;
        width: 270px;
        height: 1000px;
        display: flex;
        flex-direction: column;
        background: #fff;

        &-item {
          height: 41px;
          font-size: 14px;
          color: #525252;
          border-bottom: 1px solid #f5f5f5;
          padding: 12px 0px 12px 20px;
        }
      }

    }

    .burger-icon {
      display: block;
      width: 20px;
      height: 20px;
      cursor: pointer;
    }
  }

}

</style>



