<template>
  <footer class="app-footer">
    <div class="footer-box">
      <div class="footer-box-info">
        <svg-icon
          icon-class="white_logo"
          class="logo-icon"
        ></svg-icon>
        <div class="footer-info">
          <div class="footer-info-qrcode">
            <svg-icon
              icon-class="qrcode"
              class="qrcode-icon"
            ></svg-icon>
          </div>
          <div>
            <div class="footer-info-scan">掃一掃，趕快加入我們的社群</div>
            <div class="footer-info-way">Redesign and Born in a different way!</div>
          </div>
        </div>
        <div class="footer-copyright">Reborn 設計學院 © All Rights Reserved.</div>
      </div>
      <div class="footer-link-nav">
        <div class="footer-link">
          <router-link class="footer-link-title" to="/">關於我們</router-link>
          <div class="footer-link-inner">
            <router-link to="/">最新消息</router-link>
            <router-link to="/">團隊介紹</router-link>
            <router-link to="/">使用者條款</router-link>
            <router-link to="/">隱私權政策</router-link>
          </div>
        </div>
        <div class="footer-link">
          <router-link class="footer-link-title" to="/">學員服務</router-link>
          <div class="footer-link-inner">
            <router-link to="/">常見問題</router-link>
            <router-link to="/">聯繫客服</router-link>
          </div>
        </div>
        <div class="footer-link">
          <router-link class="footer-link-title" to="/">社群頻道</router-link>
          <div class="footer-link-inner">
            <router-link to="/">Facebook</router-link>
            <router-link to="/">YouTube</router-link>
            <router-link to="/">Slack</router-link>
            <router-link to="/">LINE</router-link>
          </div>
        </div>
      </div>
      <div class="footer-copyright-mobile">Reborn 設計學院 © All Rights Reserved.</div>
    </div>
  </footer>
</template>
<script>
import ConfirmButton from '@/components/button/ConfirmButton'

export default {
  name: 'AppFooter',
  components: {
    ConfirmButton
  },
  data () {
    return {

    }
  }
}
</script>
<style lang="scss" scoped>
.app-footer {
  width: 100%;
  background: #2b5eb3;

  .footer {

    &-box {
      max-width: 1200px;
      width: 100%;
      padding: 60px;
      display: flex;
      justify-content: space-between;
      margin: auto;
      color: #fff;

      .logo-icon {
        height: 50px;
        width: 120px;
        margin-bottom: 34px;
      }

      &-info {
        width: 60%;
      }
    }

    &-info {
      display: flex;
      align-items: center;
      margin-bottom: 34px;

      &-qrcode {
        width: 97px;
        height: 97px;
        background: #fff;
        padding: 9px;
        margin-right: 16px;
        border-radius: 6px;

        .qrcode-icon {
          width: 100%;
          height: 100%;
        }
      }

      &-scan {
        font-size: 14px;
      }

      &-way {
        font-size: 16px;
      }
    }

    &-copyright {
      font-size: 12px;
    }

    &-link-nav {
      width: 40%;
      display: flex;
      justify-content: space-between;
    }

    &-link-inner {
      display: flex;
      flex-direction: column;

      & a:hover {
        color: #fa6400;
      }
    }

    &-link-title {
      cursor: text;
      color: #fff;
    }

    &-link {
      display: flex;
      flex-direction: column;

      & a {
        font-size: 14px;
        margin-bottom: 12px;
      }
    }

    &-copyright-mobile {
      display: none;
    }
  }
}

@media( max-width: 500px ){

.app-footer {
  padding: 0px;

  .footer {

    &-box {
    padding: 20px 14px 40px;
    flex-direction: column;

      .logo-icon {

      }

      &-info {
        width: 100%;
      }
    }

    &-info {

      &-qrcode {
        width: 84px;
        height: 84px;
        padding: 7px;

        .qrcode-icon {

        }
      }

      &-scan {

      }

      &-way {

      }
    }

    &-copyright {
      display: none;
    }

    &-link-nav {
      width: 100%;
      flex-direction: column;
      margin-bottom: 40px;
    }

    &-link-inner {
      flex-direction: row;

      & a {
        margin-right: 12px;
      }
    }

    &-copyright-mobile {
      display: block;
      font-size: 12px;
    }
  }
}

}

</style>



